.pagination2{
    display:flex;
    justify-content:center;
    gap:0 1rem;
    list-style:none;

}
.pagination2-item{
    padding: 0.25rem 0.5rem;
    color:white;
    border:2px solid white;
    cursor:pointer;
}
.pagination2-item.active{
    background-color: white;
    color:black;
}